.card {
  background: linear-gradient(116.72deg, #132343 25.31%, #0B1B3A 84.28%);
  box-shadow: 4.01833px 9.18476px 13.7771px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  width: 100%;
  transition: all .25s;
  margin: 0;
  cursor: pointer;
  overflow: hidden;
  @media(min-width: 600px) {
    grid-gap: 16px;
    grid-template-columns: minmax(auto, 256px) minmax(0, 1fr);
  }
  @media(min-width: 800px) {
    grid-gap: 34px;
  }
  @media(min-width: 1500px) {
    grid-template-columns: minmax(auto, 170px) minmax(0, 1fr);
    height: 100%;
  }
  @media(min-width: 1600px) {
    grid-template-columns: minmax(auto, 256px) minmax(0, 1fr);
  }

  * {
    box-sizing: border-box;
  }
  &:hover {
    background: linear-gradient(116.72deg, #1F3053 25.31%, #102042 84.28%);
    box-shadow: 4.01833px 9.18476px 13.7771px rgba(0, 0, 0, 0.15);
    transform: scale(1.02);
    .cardSubTitle {
      opacity: 1;
    }
  }

  .cardData {
    width: 100%;
    height: 100%;
    display: grid;
    padding: 24px 16px;
    grid-gap: 0;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: auto minmax(0, 1fr) auto;
    align-content: start;
    @media(min-width: 800px) {
      padding: 24px 24px 16px 0;
    }
  }

  .cardImage {
    width: 100%;
    min-height: 208px;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
    @media(min-width: 1500px) {
      min-height: 150px;
    }
    @media(min-width: 1600px) {
      min-height: 208px;
    }
  }

  .greenButton {
    display: flex;
    align-items: center;
    grid-gap: 15.5px;
    font-family: 'Hind';
    font-style: normal;
    font-weight: 600;
    font-size:18px;
    line-height: 150%;
    text-align: center;
    color: #12B2A6;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all .25s;
    text-align: left;
    @media(min-width: 1200px) {
      justify-content: flex-end;
    }
    img {
      margin-bottom: 4px;
    }
    &:hover {
      opacity: .8;
    }
    @media(min-width: 1500px) {
      grid-gap: 10px;
      font-size: 16px;
    }
    @media(min-width: 1750px) {
      grid-gap: 12px;
      font-size: 18px;
    }
    @media(min-width: 1850px) {
      grid-gap: 15.5px;
      font-size: 16px;
    }
  }

  .goToChannel {
    height: auto !important;
    font-size: 20px !important;
    line-height: 24px !important;
    width: auto !important;
    padding: 8px 12px !important;
    line-height: 12px !important;
    position: absolute;
    font-weight: 400 !important;
    z-index: 1;
    top: 12px;
    background: #E6E6E6;
    color: #000000 !important;
    right: 16px;
    &:hover {
      background: #E6E6E6;
      color: #000000 !important;
    }

    &.recent {
      background: #272D39;
      color: #FFFFFF !important;
      width: auto !important;
      font-size: 18px !important;
    }
    &.recommended {
      width: auto !important;
      font-size: 16px !important;
    }
  }
  .hide {
    opacity: 0;
  }

  .cardTitle {
    font-family: 'Hind';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: #F7F9FD;
    margin: 0 0 16px;
  }

  .tutor {
    width: fit-content;
    order: 0;
    flex-grow: 0;
    flex: none;
    margin: 0 0 37px;
    display: flex;
    gap: 16px;
    align-items: flex-start;

    .tutorAvatar {
      width: 48px;
      height: 48px;
      border-radius: 100%;
      margin: 0;
      background: center / cover no-repeat;
    }

    .tutorName {
      font-weight: 600;
      font-size: 20px;
      line-height: 120%;
      letter-spacing: 0.02em;
      color: #FFFFFF;
      display: block;
      width: 100%;
    }

    .learnWith {
      font-family: 'Hind';
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: -0.01em;
      color: #FFFFFF;
      opacity: 0.6;
    }
  }

  .cardSubTitle {
    opacity: 0;
    transition: opacity 0.2s ease-in;
    font-family: 'Hind';
    font-weight: 600;
    font-size: 20px;
    line-height: 160%;
    color: #FEFEFE;
    margin: 10px 0 0;
    transition: all .25s;
  }
  .cardList {
    @media(min-width: 1050px) {
      display: grid;
      grid-template-columns: minmax(0, 1fr) auto;
      align-items: center;
      grid-gap: 8px;
    }
    .list {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      align-self: end;
      flex-wrap: wrap;
      gap: 8px;
      color: #FFFFFF;
      opacity: 0.6;
    }

    .childEl {
      font-family: 'Hind';
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 160%;
      color: #FEFEFE;

    }

    .dotDivider {
      height: 4px;
      width: 4px;
      background: #7B8292;
      border-radius: 100%;
    }
  }
}