.catalog {
    .header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 32px 20px 0;
        @media (min-width: 800px) {
          padding: 32px 32px 0;
        }
        @media (min-width: 1200px) {
          padding: 32px 64px 0;
        }

        .title {
            font-family: TT Commons;
            font-size: 32px;
            line-height: 100%;
            color: #F7F9FD;
            margin: 0;
            text-align: center;
            @media (min-width: 840px) {
              text-align: left;
            }
        }

        .seeAll {
            font-family: TT Commons;
            font-weight: 300;
            font-size: 18px;
            color: #FEFEFE;
            margin-left: auto;

            a {
                color: inherit;
                text-decoration: none;
            }

            @media (max-width: 834px) {
                & {
                    display: none;
                }
            }
        }
    }

    .catalogContainer {
      padding: 40px 20px;
      @media(min-width: 800px) {
        display: grid;
        grid-template-columns: minmax(auto, 228px) minmax(0, 1fr);
        padding: 40px 32px;
      }
      @media(min-width: 1200px) {
        grid-template-columns: minmax(auto, 400px) minmax(0, 1fr);
      }
      @media(min-width: 1400px) {
        display: grid;
        padding: 40px 64px;
      }
      @media(min-width: 1500px) {
        grid-template-columns: minmax(auto, 228px) minmax(0, 1fr);
      }
    }

    .subTitle {
        font-family: TT Commons;
        font-weight: 400;
        font-size: 24px;
        line-height: 145%;
        color: #FEFEFE;
        max-width: 482px;
        margin: 0 auto;
        text-align: center;
        @media (min-width: 800px) {
          text-align: left;
          margin: 0;
        }
    }

    .filterLabel {
      font-family: 'Hind';
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 24px;
      color: #8A8AA0;
      display: block;
      text-transform: uppercase;
      margin-bottom: 8px;
    }

    .filterContainer {
        display: none;
        justify-content: flex-start;
        flex-wrap: wrap;
        gap: 52px;
        padding: 0;
        margin: 0 auto;

        @media(min-width: 840px) {
          display: block;
          margin: 0;
          width: 100%;
        }

        .filter {
          display: grid;
          grid-template-columns: auto minmax(0, 1fr);
          grid-gap: 16px;
          align-items: center;
          width: 100%;
          padding: 13px 0;
          background: none;
          border: none;
          color: white;
          text-align: left;
          cursor: pointer;
          font-size: 16px;
          line-height: 20px;
          &:before {
            width: 14px;
            height: 14px;
            background-color: #FFFFFF;
            border: 1px solid #878D99;
            border-radius: 2px;
            content: ''
          }
          &.filterActive:before {
            background-color: #E5067C;
            background-image: url('../../assets/images/checkMark.svg');
            background-size: contain;
            background-position: center center;
            background-repeat: no-repeat;
          }
        }
    }

    .carousel {
        position: relative;
    }

    .seeAllMobile {
        font-family: TT Commons;
        font-weight: 700;
        font-size: 18px;
        letter-spacing: 0.4px;
        color: #F7F9FD;
        border: 1px solid #F7F9FD;
        border-radius: 40px;
        padding: 8px 16px;
        width: fit-content;
        margin: 16px auto 0;

        a {
            color: inherit;
            text-decoration: none;
        }

        @media (min-width: 835px) {
            & {
                display: none;
            }
        }
    }
}

.selectAll {
  font-family: 'Hind';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #12B2A6;
  background: none;
  border: none;
  padding: 19px 0 0;
  cursor: pointer;
  transition: all .25s;
  &:hover {
    color: darken(#12B2A6, 15%);
  }
}
.selectAllDisable {
  cursor: default;
  opacity: 0.5;
  &:hover {
    color: #12B2A6;
  }
}
