.card {
    font-family: TT Commons;
    color: #FEFEFE;
    cursor: pointer;
    position: relative;

    &.darkTexts {
        color: #05112a;
    }

    .cardImage {
        height: 178px;
        position: relative;
        overflow: hidden;
        border-radius: 8px;

        .cardThumb {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transform: scale(1);
            transition: .3s ease-in-out;

            &:hover {
                transform: scale(1.1);
            }
        }

        .overlay {
            position: absolute !important;
            width:100%;
            height: 100%;
            z-index: 0 !important;
        
            .spinner {
                position: absolute;
                left: calc(50% - 20px);
                top: calc(50% - 20px);
            }
        }

        .cardSubTitle {
            font-size: 16px;
            margin-bottom: 20px;
        }

        .cardActions {
            position: absolute;
            top: 8px;
            right: 8px;
        }

        .goToChannel {
            height: auto !important;
            font-size: 13px !important;
            font-weight: 500 !important;
            width: auto !important;
            padding: 8px 10px !important;
            line-height: 12px !important;
            background-image: none !important;
            background: #E6E6E6 !important;
            color: #000000 !important;

            &.recent {
                background: #272D39 !important;
                color: #FFFFFF !important;
                width: auto !important;
            }
        }
    }

    .cardData {
        margin-left: 8px;

        .cardHeader {
            display: flex;

            .tutorAvatar {
                width: 45px;
                height: 45px;
                border-radius: 50%;
                margin-right: 8px;
                position: relative;
                top: -8px;
                background: center / cover no-repeat;
                border: 2px solid #05102A;
                &.lightBorder {
                  border: 2px solid #FFFFFF;
                }
            }

            .cardTitle {
                font-size: 24px;
                font-weight: 700;
                margin: 8px 0 0 0;
            }
        }

        .tutorName {
            font-weight: 500;
            font-size: 18px;

            .learnWith {
                font-size: 17px;
                margin-right: 6px;
                opacity: 0.6;
            }
        }

        .cardList {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            align-content: center;
            gap: 20px;
            font-weight: 400;
            font-size: 14px;

            &.cardListWithoutTutor {
                margin-top: 8px;
            }

            .childEl {
                position: relative;
                &:not(:last-child)::after {
                    position: absolute;
                    content: '.';
                    left: 114%;
                    top: 25%;
                    transform: translate(-50%,-50%);
                    z-index: 1;
                    pointer-events: none;
                    font-size: 30px;
                    color: #7B8292;
                }

                .cardActionType {
                    height: 10px;
                    margin-right: 5px;
                }
            }
        }
    }
}
