.flag {
    width: 164px;
    height: 164px;
    border-radius: 8px;
    position: relative;
    padding: 0;
    border: 0;
    cursor: pointer;
    overflow: hidden;

    &:hover {
        .flagImage {
            transform: scale(1.1);
        }
    }

    .flagImage {
        width: 100%;
        height: 100%;
        background-image: url(https://d37sy4vufic209.cloudfront.net/images/flags/flags-square-sprite-50@2x-f5de1bfbb74095602420bcd470458ed2.png);
        background-size: 1032% auto;
        object-fit: cover;
        transform: scale(1);
        transition: .3s ease-in-out;
    }

    .flagRectangle {
        background: linear-gradient(359.79deg, #05112A 0.1%, rgba(5, 17, 42, 0) 82.72%);
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }

    .flagBottom {
        background: rgba(22, 41, 82, 0.5);
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 58px;
        display: flex;
        align-items: flex-end;

        .langName {
            font-family: TT Commons;
            font-size: 18px;
            font-weight: 700;
            color: #FEFEFE;
            margin-bottom: 11px;
            margin-left: 11px;
        }
    }
}