.sliderBack {
    position: absolute;
    top: calc(50% - 38px);
    left: -24px;
    transform: rotate(180deg);

    width: 46px;
    height: 46px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px;

    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 24px;
    border: 0;

    fill: #05112A;

    &:disabled {
        opacity: 0.5;
    }

    @media (max-width: 414px) {
        left: -5px;
    }
}

.sliderNext {
    position: absolute;
    top: calc(50% - 38px);
    right: -24px;

    width: 46px;
    height: 46px;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px;

    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 24px;
    border: 0;

    &:disabled {
        opacity: 0.5;
    }

    @media (max-width: 414px) {
        right: -5px;
    }
}

.dotGroup {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;

    @media (max-width: 650px) {
        & {
            display: none;
        }
    }

    :global(.carousel__dot) {
        height: 6px;
        width: 6px;
        border-radius: 50%;
        border: 0;
        background-color: #989BA3;
        padding:0px;
    }
    :global(.carousel__dot--selected) {
        height: 8px;
        width: 8px;
        background: #FEFEFE;
    }

    &.darkTexts {
        :global(.carousel__dot--selected) {
            background: linear-gradient(90deg, #0AD6CA 0%, #597EFF 100%);
        }
    }
}

.gridList {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(164px, 1fr));
    grid-column-gap: 24px;
    grid-row-gap: 28px;
}

.sliderContainer {
    :global(.carousel__slide-focus-ring) {
        outline: none !important;
    }
}