.grid {
    &.darkTexts .header {
      margin-bottom: 16px;
    }
    .header {
        display: flex;
        align-items: center;
        margin-bottom: 8px;

        .title {
            font-family: TT Commons;
            font-size: 32px;
            color: #FEFEFE;
            margin: 0;
        }
    }

    .subTitle {
        font-family: TT Commons;
        font-weight: 300;
        font-size: 22px;
        color: #FEFEFE;
        margin-bottom: 16px;
    }

    .filterWrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto 18px;
        gap: 40px;

        &.darkTexts {
            margin: 0 auto 40px;
        }

        .divider {
          height: 40px;
          background-color: #C4C4C4;
          width: 1px;
          display: none;
          @media(min-width: 1023px) {
            display: block;
          }
        }

        .filterContainer {
            display: none;
            align-items: center;
            justify-content: center;
            gap: 26px;
            padding: 0;
            margin: 0;

            &.darkTexts {
              gap: 32px;

              @media(min-width: 845px) {
                display: none;
              }
              @media(min-width: 1024px) {
                display: grid;
              }
              @media(min-width: 1200px) {
                gap: 52px;
              }
            }

            @media(min-width: 845px) {
              display: grid;
            }

            .filter {
                background: none;
                border-radius: 28px;
                padding: 3px 10px;
                font-family: TT Commons;
                font-weight: 400;
                font-size: 22px;
                line-height: 24px;
                display: flex;
                align-items: center;
                justify-content: center;
                border: none;
                color: #FFFFFF;
                cursor: pointer;
                width: max-content;
                transition: all .25s;

                &.darkTexts {
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 20px;
                    color: rgba(5,17,42, 0.6);
                    background: #DFE2E8;
                    border-radius: 24px;
                    padding: 8px 14px;
                    @media(min-width: 1200px) {
                      font-size: 18px;
                    }
                }

                &.filterActive {
                    background: rgba(73, 73, 73, 0.67);
                    cursor: default;

                    &.darkTexts {
                        background: linear-gradient(135deg, #EE512E 0%, #DF006B 63.44%, #BD0071 100%);
                        color: #FEFEFE;
                    }
                }
                &:not(.filterActive):hover {
                  background: rgba(73, 73, 73, 0.44);

                  &.darkTexts {
                    background: #c0c5cf;
                  }
                }
            }
        }

        .seeAll {
            font-family: TT Commons;
            font-weight: 400;
            font-size: 18px;
            color: #FEFEFE;
            margin-left: auto;
            background: transparent;
            border: 0;
            cursor: pointer;
            padding: 0;

            @media (max-width: 844px) {
                & {
                    display: none;
                }
            }
        }
      }

    .carousel {
        position: relative;
    }

    .learnMoreContainer {
        text-align: center;

        .learMore {
            font-family: TT Commons;
            font-weight: 400;
            font-size: 22px;
            line-height: 24px;
            color: #FFFFFF;
            padding: 6px 29px;
            cursor: pointer;
            background: rgba(73, 73, 73, 0.4);
            box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.25);
            border: none;
            border-radius: 10px;
            margin-top: 22px;
            margin-bottom: 1px;

            &:hover {
                padding: 7px 35px;
                margin-top: 21px;
                margin-bottom: 0px;
            }
        }

        @media (max-width: 844px) {
            & {
                display: none;
            }
        }
    }

    .seeAllMobileContainer {
        margin-top: 32px;
        text-align: center;

        .seeAllMobile {
            font-family: TT Commons;
            font-weight: 700;
            font-size: 18px;
            letter-spacing: 0.4px;
            color: #F7F9FD;
            border: 1px solid #F7F9FD;
            border-radius: 40px;
            padding: 8px 16px;
            cursor: pointer;
            background: transparent;
        }

        @media (min-width: 845px) {
            & {
                display: none;
            }
        }
    }

    .goToChannelsCatalog {
      font-family: 'Hind';
      border: none;
      outline: none;
      font-style: normal;
      font-size: 18px;
      font-weight: 600;
      line-height: 24px;
      background: linear-gradient(135deg, #EE512E 0%, #DF006B 63.44%, #BD0071 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      cursor: pointer;
      &:hover {
        background: linear-gradient(135deg, #BD0071 0%, #DF006B 63.44%, #EE512E 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
      @media(min-width: 1200px) {
        font-size: 18px;
      }
    }
}
